import { graphql } from 'gatsby';
import styled from 'styled-components';
import React from 'react';
import PostGrid from '../components/PostGrid';
import Navbar from '../components/Navbar';
import { GlobalStyle } from '../components/styles';

import { QueryRes } from '../components/types';
import Seo from '../components/seo';

const Wrapper = styled.div`padding-top: 80px;`;

const Title = styled.div`
  font-size: 30px;
  text-align: center;
  margin: 30px 0px 70px 0px;
  padding: 20px;
  width: fit-content;
  left: 50%;
  position: relative;
  transform: translate(-50%);
  border-bottom: 10px solid #ff5757;
  color: #FFF
`;

interface BlogProps {
  data: QueryRes
}

const Blog: React.FC<BlogProps> = ({ data }: BlogProps) => (
  <>
    <Seo title="Blog" />
    <GlobalStyle />
    <div>
      <Navbar theme={{
        background: '#181818',
        mobileBackground: '#131313',
        shadow: '20px rgba(0, 0, 0, 0.05)',
        fontColor: '#bbb',
      }}
      />
      <Wrapper>
        <Title>
          LASTEST POSTS
        </Title>
        <PostGrid mds={data.mds} />
      </Wrapper>
    </div>
  </>
);

export const query = graphql`
query MyQuery {
  mds: allMdx {
    list:edges {
      md: node {
        fields: frontmatter {
          desc
          date
          title
        }
        filename: slug
      }
    }
  }
}
`;

export default Blog;
